.destinations {
  overflow-x: hidden;
  padding: 96px 0 100px;

  &__row {
    display: flex;
    column-gap: 50px;
  }

  &__content {
    max-width: 456px;
  }

 

  &__title {
    margin-bottom: 15px;
    font-size: 55px;
    font-weight: 600;
    span {
      font-size: 30px;
    }
  }

  &__text {
    margin-bottom: 60px;
    font-size: 25px;
    font-weight: 600;
    color: var(--secondary);
  }

  .owl-item {
    display: flex;
    justify-content: center;
  }

  &__slider {
    position: relative;
    width: 1110px;
    flex-shrink: 0;

    .slider {
      height: 456px;
      & .center .slider__item {
        margin-top: 0;
        width: 380px;
        height: 456px;
      }

      &__item {
        margin-top: 27px;
        width: 335px;
        height: 402px;
        object-fit: cover;
        border-radius: 35px;
        transition: 0.2s ease-in;
      }
      &__btn {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        display: block;
        z-index: 9;
        width: 68px;
        height: 68px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        background: linear-gradient(172deg, #ff7b4e 0%, #ff584e 100%);

        &--prev {
          left: -27px;
          padding-left: 26px;
          justify-content: flex-start;
        }

        &--next {
          right: -27px;
          padding-right: 26px;
          justify-content: flex-end;
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  .destinations__slider {
    width: 900px;
  }
}

@media (max-width: 1000px) {
  
  .destinations__slider {
    width: 600px;
  }

  .slider {
    height: 304px;
  }

  .slider .slider__item {
    margin-top: 18px;
    width: 223px;
    height: 268px;
  }

  .destinations__slider .slider .center .slider__item {
    margin-top: 0;
    width: 253px;
    height: 304px;
  }

  
}

@media (max-width: 850px) {

  .destinations {
    padding: 40px 0 50px;
  }

  .container-right {
    padding-right: 15px;
  }

  .destinations__row {
    flex-direction: column;
    row-gap: 30px;
  }


  .destinations__slider {
    width: 100%;
  }

  

  .slider__btn--prev {
    left: -15px;
  }

  .slider__btn--next {
    right: -15px;
  }

  .slider .slider__btn--prev {
    left: -11px;
  }

  .slider .slider__btn--next {
    right: -11px;
  }

  .destinations__title {
    margin-bottom: 10px;
    font-size: 35px;
    
  }

  .destinations__title span {
    font-size: 20px;
    display: block;
  }

  .destinations__text {
    margin-bottom: 30px;
    font-size: 20px;
  }

  .btn {
    padding: 20px 35px;
    border-radius: 20px;
    font-size: 18px;
  }

}