.subscribe {
  padding: 35px 0 50px;
  /* .subscribe__content */

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  /* .subscribe__title */

  &__title {
    margin-bottom: 60px;
    max-width: 494px;
    font-size: 55px;
    font-weight: 600;
  }

  /* .subscribe__form */

  &-form {
    padding: 12px 15px 12px 46px;
    border-radius: 35px;
    background-color: #fff;
    display: flex;
    column-gap: 20px;
    /* .subscribe-form__input */

    &__input {
      font-size: 24px;
      font-weight: 500;
      

      &::placeholder {
        color: var(--secondary);
      }
    }

    /* .subscribe-form__btn */

    
    }
  }


  @media (max-width: 850px) {
    .subscribe {
      padding: 25px 0 30px;
    }

    .subscribe__title {
      margin-bottom: 30px;
      font-size: 35px;
    }

    .subscribe-form {
      padding: 10px 10px 10px 20px;
      border-radius: 25px;
      column-gap: 10px;

    }
  }

  @media (max-width: 480px) {
    .subscribe-form {
      padding: 10px;
      flex-direction: column;
      row-gap: 10px;
      max-width: 320px;
    }
    .subscribe-form__input {
      padding: 15px 0;
      background-color: #f0f0f0;
      text-align: center;
      border-radius: 20px;
      font-size: 18px;
    }
  }

  @media (max-width: 460px) {
    .customers__img img {
      max-width: 100%;
    }
  }