.customers {
  padding: 100px 0 133px;

  /* .customers__row */

  &__row {
    display: flex;
    column-gap: 68px;
  }

  /* .customers__img */

  &__img {
    max-width: 478px;
    
    img {
      border-radius: 60px;
    }
  }

  /* .customers__content */

  &__content {
  }

  /* .customers__title */

  &__title {
    margin-bottom: 35px;
    font-size: 45px;
    font-weight: 500;
  }
}

.review {
  max-width: 490px;
  /* .review__client */

  &__client {
    display: flex;
    align-items: center;
    column-gap: 33px;
    margin-bottom: 60px;
  }

  /* .review__client-avatar */

  &__client-avatar {
    overflow: hidden;
    width: 134px;
    height: 134px;
    border-radius: 50%;
    background-color: #ff794e;
  }

  /* .review__client-desc */

  &__client-desc {
  }

  /* .review__client-desc-name */

  &__client-desc-name {
    font-size: 33px;
    font-weight: 500;
    margin-bottom: 10px;
  }

  /* .review__client-desc-country */

  &__client-desc-country {
    font-size: 25px;
    color: var(--secondary);
  }

  /* .review__text */

  &__text {
    margin-bottom: 30px;
    color: var(--secondary);
    font-size: 22px;
    font-weight: 500;
  }

  /* .review__rating */

  &__rating {
  }
}

@media (max-width: 850px) {
  .customers {
    padding: 50px 0 60px;
  }

  .customers__row {
    column-gap: 30px;
  }
  
  .customers__title {
    margin-bottom: 25px;
    font-size: 35px;
  }

  .customers__img img {
    border-radius: 40px;
    max-width: 250px;
  }

  .review__client {
    column-gap: 30px;
    margin-bottom: 20px;
  }

  .review__client-avatar {
    width: 80px;
    height: 80px;
  }

  .review__client-desc-name {
    margin-bottom: 5px;
    font-size: 23px;
  }

  .review__client-desc-country {
    font-size: 18px;
  }

  .review__text {
    margin-bottom: 20px;
    font-size: 18px;
  }

  .review__rating {
    max-width: 200px;
  }
}

@media (max-width: 640px) {
  .customers__row {
    flex-direction: column-reverse;
    row-gap: 30px;
  }
}