.cta {
  padding: 100px 0;
  /* .cta__plate */

  &__plate {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 580px;
    padding: 40px 15px;
    background-color: #fff;
    border-radius: 60px;

    &::before,
    &::after {
      content: '';
      position: absolute;
      width: 63px;
      height: 83px;
    }

    &::before {
      left: 74px;
      bottom: 56px; 
      background-image: url('./../img/cta/cta-el-01.svg');

    }

    &::after {
      top: 74px;
      right: 80px;
      background-image: url('./../img/cta/cta-el-02.svg');

    }
  }

  /* .cta__title */

  &__title {
    font-size: 45px;
    font-weight: 500;
    margin-bottom: 30px;
  }

  /* .cta__text */

  &__text {
    font-size: 30px;
    margin-bottom: 60px;
    color: var(--secondary);
  }

  /* .cta__button */

 
}

@media (max-width: 850px) {
  .cta {
    padding: 50px 0;
  }

  .cta__title {
    font-size: 35px;
    margin-bottom: 20px;
  }

  .cta__text {
    margin-bottom: 30px;
    font-size: 20px;
  }

  .cta__plate {
    min-height: 380px;
    padding: 20px 15px;
    border-radius: 40px;
  }
}