.footer {
  // .logo--footer {
  //   color: #ff7c4e;
  // }
  padding: 56px 0 74px;
  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 80px;

    .logo {
      font-size: 20px;
      font-weight: 700;
      color: var(--orange);

      a {
        color: inherit;
      }

      
    }

    &__list--footer {
      display: flex;
      column-gap: 80px;
      font-size: 15px;
      font-weight: 500;
      color: #fff;

      a {
        position: relative;
        color: var(--secondary);

      }

      a.active::after, a:hover::after {
        display: none;
      }

      a.active, a:hover {
        color: var(--orange);
      }

    }

    &__login {
      display: flex;
      align-items: center;
      column-gap: 25px;
      font-size: 15px;
      font-weight: 500;
      color: #fff;

      .reg-footer {
        color: #838b89;
      }

      .btn {
        display: inline-block;
        padding: 13px 44px;
        background: linear-gradient(172deg, #ff7b4e 0%, #ff584e 100%);
        -webkit-border-radius: 100px;
        -moz-border-radius: 100px;
        border-radius: 100px;
        font-size: 15px;
        font-weight: 500;

        &:hover {
          color: #fdf389;
          transition: color 0.2s ease-in;
        }

        &:active {
          position: relative;
          top: 1px;
        }
      }
    }
  }
}

@media (max-width: 1000px) {
  .footer .nav {
    column-gap: 50px;
  }

  .footer .nav__list--footer {
    column-gap: 50px;
  }
}

@media (max-width: 600px) {
  .footer .nav--footer {
    flex-direction: column-reverse;
    row-gap: 30px;
  }
}

@media (max-width: 480px) {
  .footer .nav__list--footer {
    flex-direction: column;
    row-gap: 15px;
    align-items: center;
  }
}

@media (max-width: 850px) {
  .footer .nav__login--footer {
    display: none;
  }
}