.header {
  position: relative;
  min-height: 940px;
  padding-top: 44px;
  background: linear-gradient(154deg, #428e5f 0%, #112120 100%);
  color: #fff;
  overflow: hidden;

  a {
    color: #fff;
  }

  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 96px;
    background-image: url('../img/header/header-bottom.svg');
    background-size: 100%;
  }

  &__top {
    margin-bottom: 86px;
  }

  &__row {
    display: flex;
  }

  &__img {
    position: relative;
    flex-grow: 1;

    &::after {
      content: '';
      position: absolute;
      top: -30px;
      right: -50px;
      width: 830px;
      height: 622px;
      pointer-events: none;
      background-image: url('./../img/header/header-elements.svg');
    }
  }

  &__img img {
    top: -181px;
    right: -87px;
    position: absolute;
    width: 894px;
    height: 940px;
    max-width: unset;
    pointer-events: none;
  }

  // NAV
  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 80px;

    .logo {
      font-size: 20px;
      font-weight: 700;
      color: #fff;

      a {
        color: inherit;
      }
    }

    &__list {
      display: flex;
      column-gap: 80px;
      font-size: 15px;
      font-weight: 500;

      a {
        position: relative;
      }

      a.active::after,
      a:hover::after {
        content: '';
        position: absolute;
        left: 0;
        right: 10px;
        bottom: -6px;
        display: block;
        height: 4px;

        border-radius: 100px;
        background-color: #c4c4c4;
      }
    }

    &__login {
      display: flex;
      align-items: center;
      column-gap: 25px;
      font-size: 15px;
      font-weight: 500;
      color: #fff;
    }
    &__toggle {
      display: none;
    }

    
  }

  &__content {
    position: relative;
    z-index: 1;
    max-width: 592px;

    .header__badge {
      margin-bottom: 7px;
      display: inline-block;
      padding: 11px 30px;
      font-size: 18px;
      font-weight: 500;
      background: rgba(255, 255, 255, 0.25);
      border-radius: 15px;
    }

    .header__title {
      font-size: 55px;
      margin-bottom: 30px;
    }

    .header__text {
      margin-bottom: 68px;
      font-size: 25px;
      font-weight: 700;
      color: rgba(255, 255, 255, 0.5);
    }
  }

  /* Nav Icon */
  .menu-icon-wrapper {
    --time: 0.1s;

    --width: 40px;
    --height: 30px;

    --line-height: 4px;
    --spacing: 6px;

    --color: #fff;
    --radius: 4px;

    /* Fixed height and width */
    /* height: var(--height); */
    /* width: var(--width); */

    /* Dynamic height and width */
    height: calc(var(--line-height) * 3 + var(--spacing) * 2);
    width: var(--width);

    display: flex;
    justify-content: center;
    align-items: center;
  }

  .menu-icon {
    position: relative;
    width: var(--width);
    height: var(--line-height);
    background-color: var(--color);
    border-radius: var(--radius);
  }

  .menu-icon::before,
  .menu-icon::after {
    content: '';
    display: block;

    position: absolute;
    left: 0;

    width: var(--width);
    height: var(--line-height);

    border-radius: var(--radius);
    background-color: var(--color);
    transition: transform var(--time) ease-in,
      top var(--time) linear var(--time);
  }

  .menu-icon::before {
    /* top: calc(var(--line-height) * -2); */
    top: calc(-1 * (var(--line-height) + var(--spacing)));
  }

  .menu-icon::after {
    /* top: calc(var(--line-height) * 2); */
    top: calc(var(--line-height) + var(--spacing));
  }

  .menu-icon.menu-icon--active {
    background-color: transparent;
  }

  .menu-icon.menu-icon--active::before,
  .menu-icon.menu-icon--active::after {
    top: 0;
    transition: top var(--time) linear,
      transform var(--time) ease-in var(--time);
  }

  .menu-icon.menu-icon--active::before {
    transform: rotate(45deg);
  }

  .menu-icon.menu-icon--active::after {
    transform: rotate(-45deg);
  }

  /* Layout */

  .mobile-icon-wrapper {
    z-index: 999;
    // display: none;
  }

  // @media (max-width: 1000px) {
  // 	.mobile-nav-btn {
  // 		display: block;
  // 	}
  // }

 
}





@media (max-width: 1269px) {
  .container-right {
    padding-left: 15px;
  }

  .container-left {
    padding-right: 15px;
  }
}

@media (max-width: 1000px) {
  .header .nav {
    &__list {
      display: none;
    }

    &__login {
      display: none;
    }

    &__toggle {
      display: block;
    }

    &--mobile {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 99;
      flex-direction: column;
      align-items: center;
      padding-top: 40px;
      padding-bottom: 40px;
      background: linear-gradient(154deg, #428e5f 0%, #112120 100%);

      & .logo {
        font-size: 34px;
      }

      & .nav__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 40px;
        font-size: 22px;

        a.active::after {
          display: none;
        }

        a.hover::after {
          display: none;
        }
      }

      & .nav__login {
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 40px;
        font-size: 22px;
      }

      & .nav__toggle {
        position: fixed;
        top: 50px;
        right: 50px;
      }
    }
  }

  .header {
    &__img {
      &::after {
        right: -300px;
      }

      img {
        position: absolute;
        top: -181px;
        right: -256px;
      }
    }
  }
}

@media (max-width: 850px) {
  .header {
    padding-top: 20px;
    min-height: unset;

    &__top {
      margin-bottom: 40px;
    }

    &__row {
      flex-direction: column;


    }
    &__img {
      &::after {
        top: 100px;
        right: 0;
      }

      img {
        position: static;
        width: 70%;
        height: auto;
      }
    }

    &__content {
      .btn--header {
        padding: 20px 30px;
        font-size: 20px;
        border-radius: 18px;
      }
    }
    &__title {
      margin-bottom: 20px;
      font-size: 35px;
    }
    &__text {
      margin-bottom: 40px;
      font-size: 20px;
      max-width: 400px;
    }

    &__badge {
      margin-bottom: 5px;
      padding: 8px 20px;
      font-size: 16px;
      border-radius: 10px;
    }
  }
  
  .header .btn--login {
    padding: 13px 44px;
    font-size: 15px;
    font-weight: 500;
    border-radius: 100px;
  }
