  //   font-size: 15px;
  //   font-weight: 500;
  // }

}

@media (max-width: 460px) {
  .header .nav--mobile .nav__toggle {
    
    top: 20px;
    right: 15px;
  }

  .nav--mobile .nav__list,
  .nav--mobile .nav__login {
    row-gap: 20px;
  }
}